.custom-dropdown {
  background-color: #fff;
  flex-direction: column;
  width: 80%;
  //min-width: 300px;
  height: min-content;
  //margin-bottom: 5px;
  color: #555;
  p {
    line-height: 1.5em;
    font-size: 1em;
    margin-top: 8%;
    text-align: center;
    text-align: left;
  }
}

.custom-dropdown-clickable {
  display: flex;
  width: 100%;
  height: 100%;
  //padding: 0 10px;
  padding: 0;
  align-items: center;
}

.custom-dropdown-menu {
  flex-direction: row;
  display: flex;
  cursor: pointer;
  height: 45px;
  align-items: center;
  border: 1px solid transparent;
  &:hover {
    //border: 1px solid #00008f;
    color: #ffc200;
  }
}

.custom-dropdown-menu-open {
  flex-direction: row;
  display: flex;
  cursor: pointer;
  height: 45px;
  align-items: center;
  border-bottom: 1px solid #F5F5F5;
  border-top: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
  &:hover {
    color: #ffc200;
  }
}

.custom-dropdown-header {
  font-size: 1rem;
  //text-transform: capitalize;
  font-weight: bold;
  color:inherit;
  min-width: max-content;
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
}

.custom-dropdown-header-open {
  font-size: 1rem;
  //text-transform: capitalize;
  font-weight: bold;
  color: #555;
  min-width: max-content;
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
  &:hover {
    color: #ffc200;
  }
}
