.container-for-circle {
  width: 45vw;
  height: 29.7vh;
  overflow: hidden;
  padding: 0;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  perspective: 500px;
  /* background: #40f; */
  background: #fff;
  border-radius: 30px;

  p.text-below-circle {
    font-size: .6rem;
    line-height: 1.2em;
    font-weight: bold;
    opacity: .5;
    //margin: 25% 3% 0 0;
    position: absolute;
    z-index: 1;
  };
  &:hover {
    p.text-below-circle {
      opacity: 1;
      color: #ffc200;
      transition: 300ms ease;
    };
  }
}

.container {
  /* width: 400px; height: 300px; */
  width: 45vw;
  height: 29.7vh;
  display: flex;
  place-content: center;
  place-items: center;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 30px;
}

.item {
  width: 100px;
  height: 100px;
  background: #ffc200;
  border-radius: 90px;
  cursor: grab;
  z-index: 2;
}

.within-circle {
  color: #555;
  p.text {
    font-size: .9em;
    font-weight: bold;
    margin: 38% 0 0 0;
  }
}

